<template>      
      <div class="echart" id="china_map" :style="{float:'left',width: '100%', height: '450px'}"></div>   
</template>

<script>
import axios from 'axios'
import echarts from  "echarts";
import 'echarts/map/js/china.js'
import backUrl from "../constant/constant"

export default{
  data(){
    return{
      dataList:[],
      responseData:[]
    }
  },methods:{
    initEchart() { 
      var myChart = echarts.init(document.getElementById('china_map'));
        axios.post(backUrl.backend_url +"covid19/selectCovid19Data",{
          "countryName": "中国",
          "areaType": 2
        }).then(response =>{
          this.responseData = response.data.data;
          for(let i = 0;i < this.responseData.length ; i ++){
              this.dataList[i] = {name:this.responseData[i].provinceName,value:this.responseData[i].nowConfirm};
          }
          var option = {
                tooltip: {
                    //数据格式化
                    formatter:function(params, callback){
                        return params.seriesName+'<br />'+params.name+'：'+params.value
                    }
                },
                visualMap: {
                  show:true,
                  left: 26,
                  bottom: 40,
                  showLabel:true,
                  pieces: [
                    {
                      gte: 100,
                      label: ">= 1000",
                      color: "#cc0000"
                    },
                    {
                      gte: 500,
                      lt: 999,
                      label: "500 - 999",
                      color: "#a61c00"
                    },
                    {
                      gte: 100,
                      lt:499,
                      label: "100 - 499",
                      color: "#c27ba0"
                    },
                    {
                      gte: 10,
                      lt: 99,
                      label: "10 - 99",
                      color: "#8e7cc3"
                    },
                    {
                      lt:10,
                      label:'<10',
                      color: "#6fa8dc"
                    }
                  ]
                },
                geo: {
                    map: 'china',
                    roam: false,//不开启缩放和平移
                    zoom:1.23,//视角缩放比例
                    label: {
                        normal: {
                            show: true,
                            fontSize:'10',
                            color: 'rgba(0,0,0,0.7)'
                        }
                    },
                    itemStyle: {
                        normal:{
                            borderColor: 'rgba(0, 0, 0, 0.2)'
                        },
                        emphasis:{
                            areaColor: 'yellow',//鼠标选择区域颜色
                            shadowOffsetX: 0,
                            shadowOffsetY: 0,
                            shadowBlur: 20,
                            borderWidth: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                },
                // 鼠标悬浮提示框
                series : [
                    {
                        name: '省份',
                        type: 'map',
                        geoIndex: 0,
                        data:this.dataList
                    }
                ]
            };
            myChart.setOption(option);
            myChart.on('click', function (params) {
                alert(params.name);
            });


        }).catch(function(error){
           console.log(error)
        })
        
        

    }
  }
}
</script>
