<template>      
      <div class="echart" id="echart-pie" :style="{float:'left',width: '100%', height: '230px',color:'#F00'}"></div>   
</template>

<script>
import axios from 'axios'
import echarts from  "echarts";
import backUrl from "../constant/constant"

export default {
      data(){
          return{
              todayChinaData:[],
          }

      },
      methods:{
      initChartPie() {          
          axios.post(
                backUrl.backend_url +'covid19/selectCovid19Data',{
                    "countryName": "中国",
                    "areaType": 1
                }
            ).then(response =>{
               this.responseData = response.data.data               
               let code = response.data.code;
               if(code === 200){
                   this.todayChinaData[0] = {value:this.responseData[0].nowConfirm,name:'现有确诊人数'};
                    this.todayChinaData[1] = {value:this.responseData[0].heal,name:'治愈人数'};
                    this.todayChinaData[2]={value:this.responseData[0].dead,name:'死亡人数'};
                    let getchart = echarts.init(document.getElementById('echart-pie'));
                        var  option = {
                                tooltip: {
                                    trigger: 'item',
                                    formatter: '{b} : {c} ({d}%)',
                                    confine: true,
                                    extraCssText:
                                        'white-space: normal; word-break: break-all;z-index:100000;'
                                },
                                series: [
                                    {
                                    type: 'pie',
                                    data: this.todayChinaData
                                    }
                                ]
                                };
                        getchart.setOption(option);
                        //随着屏幕大小调节图表
                        window.addEventListener("resize", () => {
                            getchart.resize();
                        });

               }else{
                   document.getElementById("echart-pie").innerHTML = "系统忙，请稍候重试..."
               }
               
            })
            .catch(function (error) { // 请求失败处理
                console.log(error);
            });            
        }      

      },
}
</script>