<template>      
    <div style="float:right;margin-top: 20px">            
    <el-select @change="initEchart" v-model="provinceName" filterable placeholder="请选择省份">
        <el-option
        v-for="item in options"
        :key="item"
        :label="item"
        :value="item"
        >
        </el-option>
    </el-select> 
    </div >
    <div style="float:left">------------------------------------------------------------------------</div> 
    <div  id="first-five-heal" :style="{float:'left',width: '100%', height: '300px'}"></div> 
</template>

<script>
import axios from 'axios'
import echarts from  "echarts";
import backUrl from "../constant/constant"
export default{
    data(){
        return{
          options: '',
          cities:[],
          heals:[],
          responseData:[],
          provinceName:'广东'
        }
    },
    mounted () {       
        axios.get(backUrl.backend_url +'covid19/getProvinces')
        .then(response => (this.options = response.data.data))
        .catch(function (error) { // 请求失败处理
            console.log(error);
        });
    },
    methods:{
        initEchart(data){
            this.$forceUpdate()
            axios.post(
                backUrl.backend_url +'covid19/selectCovid19Data',{
                    "countryName": "中国",
                    "provinceName": data,
                    "areaType": 3,
                    "orderType": 2
                }
            ).then(response =>{
               this.responseData = response.data.data
               for (let i = 0;i<this.responseData.length;i++){
                   this.cities[i] = this.responseData[i].cityName;
                   this.heals[i]=this.responseData[i].heal
               }               
               let getchart = echarts.init(document.getElementById('first-five-heal'));
                var  option = {
                       color:['#6d9eeb','#dd7e6b'],
                        tooltip: {},
                        
                        xAxis: {
                            
                        },
                        yAxis: {data: this.cities},
                        series: [
                            {
                            name: '治愈人数',
                            type: 'bar',
                            data: this.heals
                            }
                        ]
                        };

                getchart.setOption(option);
                //随着屏幕大小调节图表
                window.addEventListener("resize", () => {
                    getchart.resize();
                });
            })
            .catch(function (error) { // 请求失败处理
                console.log(error);
            });
        }
    }
}
</script>